import React, {useContext, useState} from 'react'
import '@styles/base/pages/page-auth.scss'
import Register from "./Register";
import {compose} from "redux";
import {connect} from "react-redux";
import {register} from "../../../../redux/reducers/auth-reducer";
import {useHistory} from "react-router-dom";
import {AbilityContext} from "../../../../utility/context/Can";
import logo from '../../../../assets/images/logo.png';
import ecoLogo from '../../../../assets/images/logo_ecoworld.png';

const RegisterContainer = (props) => {
    const history = useHistory();
    const ability = useContext(AbilityContext);
    const [personal, setPersonal] = useState(null);
    const [education, setEducation] = useState(null);
    const onSubmit = (account) => {
        const formData = {
            ...personal,
            ...education,
            ...account
        }
        props.register(formData, history, ability);
    }
    return (
        <Register setPersonal={setPersonal}
                  setEducation={setEducation}
                  logo={logo}
                  ecoLogo={ecoLogo}
                  message={props.messageAuthForm}
                  onSubmit={onSubmit}/>
    )
}

const mapStateToProps = (state) => ({
    messageAuthForm: state.appInit.messageAuthForm
});

export default compose(connect(mapStateToProps, {register}))(RegisterContainer);
