import React, {useState} from "react";
import s from "./FormsControls.module.css";
import {Field} from "redux-form";
import InputMask from 'react-input-mask';
import {FormGroup, Label, Tooltip} from "reactstrap";
import Badge from "reactstrap/es/Badge";

const FormControl = ({input, meta: {touched, error}, children, elem, props}) => {
    const hasError = touched && error;
    const isOpen = hasError && true;
    const [tooltipOpen, setTooltipOpen] = useState(false)
    return (
        <div className={`${s.formControl} ${hasError ? s.error : ""}`}>
            {children}
            <Tooltip
                placement='top'
                isOpen={isOpen}
                target={props.id}
                toggle={() => setTooltipOpen(!tooltipOpen)}
            >
                {error}
            </Tooltip>
        </div>
    );
}

export const Input = (props) => {
    const {input, meta, child, element, ...restProps} = props;
    return (
        <FormControl {...props} props={props}>
            <input {...input} {...restProps} />
        </FormControl>
    );
}

export const PhoneInputMask = (props) => {
    const {input, meta, child, element, ...restProps} = props;
    return (
        <FormControl {...props} props={props}>
            <InputMask {...input} {...restProps} mask="+7(999) 999-99-99" maskChar="_"/>
        </FormControl>
    );
}

export const createInput = (text, placeholder, id, value, isDisabled) => (
    <FormGroup>
        <Label className='form-label' for={id}>
            {text}
        </Label>
        <input placeholder={placeholder}
               id={id}
               type='text'
               className={`input-group-merge is-touched is-dirty av-valid form-control ${s.defaultInput}`}
               value={value}
               readOnly={isDisabled}
               disabled={isDisabled}
               onChange={e => e.preventDefault()}/>
    </FormGroup>
);

export const createField = (validators, component, text, props = {}) => (
    <FormGroup>
        <Label className='form-label' for={props.id}>
            {text}
        </Label>
        <Field
            validate={validators}
            component={component}
            {...props}
        />
    </FormGroup>
);